.filterControls {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 1em;
    gap: 0.8em;
  }
  
  select {
    padding: 0.6em;
    border: 1px solid #ddd;
    border-radius: 8px;
    font-size: 1rem;
    background-color: #ffffff;
    color: #333;
    transition: box-shadow 0.2s ease, border-color 0.2s ease;
  }
  
  select:focus {
    outline: none;
    border-color: #007bff;
    box-shadow: 0 0 5px rgba(0, 123, 255, 0.4);
  }
  
  select:hover {
    border-color: #007bff;
    cursor: pointer;
  }
  
  label {
    font-size: 1rem;
    color: #555;
  }
  